	padding: 0;
	margin: 0;
	width: 100vw;
	height: 100vh;
	font-family: 'Open Sans', sans-serif;
	background: #012030;
	color: white;
}

#root {
	margin: 8px;
}

h1 {
	color: #dafdba;
}
